<template>
    <div id="box">
        <!-- 头部 -->
        <div class="header">
        <div class="span11" @click="$router.go(-1)">
          <div><i class="el-icon-arrow-left" ></i> </div>
          
        </div>
        <div class="span2">
          <span @click="btn">{{bgood}}</span>
        </div>
          <div class="span3">
           <router-link to="/login"><div v-show="out==1">注册|登录</div></router-link>
            <div @click="btn1" v-show="out==2"><i class="el-icon-user-solid"></i></div>
            
        </div>
    </div>
        <!-- 轮播 -->
        <div class="content">
             <van-swipe :autoplay="3000" indicator-color="blue">
            <van-swipe-item >
                <div v-for="(item,key) in info" :key="key">
                    <img :src="'//fuss10.elemecdn.com' + item.image_url" />
                    <p>{{item.title}}</p>
                </div>
                
            </van-swipe-item>
            <van-swipe-item>
                <div v-for="(v,key) in arr" :key="key">
                    <img :src="'//fuss10.elemecdn.com' + v.image_url" />
                    <p>{{v.title}}</p>
                </div>
                
            </van-swipe-item>
           
            </van-swipe>
            <!-- 附近商家 -->
            <div class="nearby">
                <p class="el-icon-s-shop"></p>
                <p>附近商家</p>
            </div>
            <!-- 渲染 -->
            <div class="rend">
                <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                    <van-cell v-for="(v,key) in list" :key="key" @click="btn3(v.id)">
                        <div><img :src="'https://elm.cangdu.org/img/'+v.image_path" /></div>
                        <div class="okkk">
                            <div>
                                <span>品牌</span>
                                <span>{{v.name}}</span>
                                <span>保准票</span>
                            </div>
                            <div>
                                <span><el-rate
                                         v-model="v.rating"
                                        disabled
                                        text-color="#ff9900"
                                        >
                                    </el-rate></span>
                                    <span>{{v.rating}}</span>
                                <span>月售{{v.recent_order_num}}单</span>
                                <span>蜂鸟专送</span>
                                <span>准时送</span>
                            </div>
                            <div>
                                <span>￥{{v.float_minimum_order_amount}}起送/配送费约{{v.float_delivery_fee}}</span>
                                <span>{{v.distance}}/<b class="blue">{{v.order_lead_time}}</b></span>
                                
                            </div>
                        </div>
                        
                    </van-cell>
                </van-list>

            </div>
            
        </div>
        
       
<bottom></bottom>
    </div>
</template>

<script>
import bottom from '@/components/bottom'
import Head from '@/components/head/Head'
import axios from 'axios'
export default {
    name:'Home',
    components:{Head,bottom},
    data() {
        return {
            list:[],
            info:[],
            arr:[],
            bgood:'北京',
            out:'',
            loading: false,
            finished: false,
            num:0,
        }
    },
    created(){
        if(localStorage.getItem('vuex')){
            this.out=2
        }else{
            this.out=1
        }
        //页面
        axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=').then((res)=>{
            console.log(this.list)
            this.list=res.data
            
            
        })
        //轮播的图片
        axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F').then((res)=>{
            // console.log(res.data)
            this.info=res.data.splice(0,8)
            // console.log(this.info)
            this.arr=res.data

            // console.log(this.arr)
        })
    },
    methods:{
        onLoad(){
            this.num+=8;
            axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=0&limit='+this.num+'&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=').then((res)=>{
                this.list.push(...res.data)
            })
            
            setTimeout(()=>{
                this.loading=false;
            },3000)
        },
        //跳转我的页面
        btn1(){
            this.$router.push('/my')
        },
        //跳转到地址页面
        btn(){
            this.$router.push('/HelloWorld')
        },
        //跳转到商品详情页
        btn3(id){
            this.$router.push({path:'/deta',query: {id: id}})
        },
    }

}
</script>

<style lang="scss" scoped>
.header{
  box-sizing: border-box;
  width: 100%;
  height: .91rem;
  background: #3190e8;
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  z-index: 100;
  .span11{
    width: 25%;
    height:.91rem;
    font-size: .4rem;
    text-align: left;
    line-height: .91rem;
  }
  .span2{
    width: 48%;
    height: .91rem;
    font-size: .4rem;
    color: white;
    line-height: .91rem;
    text-align: center;
  }
  .span3{
    width: 25%;
    height: .91rem;
    font-size: .4rem;
    text-align: right;
    line-height: .91rem;
    color: white;
  }
}
p{
    font-size: .2rem;
}
html,body,#box{
width: 100%;
height: 100%;
}


// 轮播
.van-swipe{
  width: 100%;
  height: 3rem;
  margin-top: .91rem;
  background: white;
 }
 .van-swipe-item{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
 }
 .van-swipe-item>div{
  width: 25%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  p{
      padding: 0;
      margin: 0;
  }
      img{
          width: 1rem;
          height: 1rem;
      }
  
 }
.content{
    width: 100%;
    height: 100%;
    background: gainsboro;
}
.nearby{
    width: 100%;
    height: .5rem;
    background: white;
    display: flex;
    align-items: center;
    margin-top: .25rem;
    p{
        color: grey;
        margin: 0;
        padding: 0;
        font-size: .18rem;
    }
}
.rend{
    width: 100%;
    height:100%;
    padding-bottom: 1rem;
    .van-list{
        width: 100%;
        // height: 100%;
        list-style: none;
        padding: 0;
        margin: 0;
        background: white;
        .van-cell{
            width: 100%;
            height: 1.7rem;
            border-bottom: 0.01rem solid gainsboro;
            background: white;
            padding: 0;
            margin: 0;
            
            div{
               float: left;
                font-size: .16rem;
                padding: .05rem 0;
                &:nth-of-type(1){
                    padding-left: .1rem;
                    img{
                        width: 1.07rem;
                        height: 1.07rem;
                    }
                }
                .okkk{
                    width: 84%;
                    div{
                        height: .3rem;
                        &:nth-of-type(1){
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            span{
                                &:nth-of-type(1){
                                    display: inline-block;
                                    width: .42rem;
                                    height: 0.3rem;
                                    line-height: .3rem;
                                    background: orange;
                                }
                                &:nth-of-type(2){
                                    display: inline-block;
                                    width: 4.45rem;
                                    height: 0.3rem;
                                    line-height: .3rem;
                                    overflow: hidden;
                                    text-overflow: ellipsisl;
                                    white-space: nowrap;
                                }
                                &:nth-of-type(3){
                                    display: inline-block;
                                    width: .7rem;
                                    height: 0.3rem;
                                    line-height: .3rem;
                                    border: 0.01rem solid gainsboro;
                                    color: gray;
                                }
                            }
                        }
                        &:nth-of-type(2){
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            span{
                                &:nth-of-type(1){
                                    display: inline-block;
                                    width: 2.7rem;
                                    height: .3rem;
                                    line-height: .3rem;
                                }
                                &:nth-of-type(2){
                                    display: inline-block;
                                    width: .15rem;
                                    height: .3rem;
                                    line-height: .3rem;
                                }
                                &:nth-of-type(3){
                                    display: inline-block;
                                    width: 1.25rem;
                                    height: .3rem;line-height: .3rem;
                                    margin-left: .2rem;
                                    // padding-right: 1.91rem;
                                }
                                &:nth-of-type(4){
                                    display: inline-block;
                                    width: .8rem;
                                    height: .3rem;
                                    line-height: .3rem;
                                    background: #3190e8;
                                    border-radius:0.05rem;
                                    color: white;
                                }
                                &:nth-of-type(5){
                                    display: inline-block;
                                    width: .6rem;
                                    height: .3rem;
                                    line-height: .3rem;
                                    border: 0.01rem solid #3190e8;
                                    color: #3190e8;
                                    border-radius:0.05rem;
                                }
                            }
                        }
                        &:nth-of-type(3){
                            
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            span{
                                display: inline-block;
                                &:nth-of-type(1){
                                    
                                    width: 2.3rem;
                                    height: 0.3rem;
                                }
                                &:nth-of-type(2){
                                   
                                    width: 3.5rem;
                                    height: 0.3rem;
                                    text-align: right;
                                    .blue{
                                        color: #3190e8;
                                    }
                                }
                            }
                        }
                        
                    }
                }
            }
        }
    }
}
</style>


