<template>
    <div class="header">
        <div class="span11" v-if="agood" @click="$router.go(-1)">
          <div v-show="agood==1"><i class="el-icon-arrow-left" ></i> </div>
          <div v-show="agood==2">
            <i class="el-icon-search"></i>
          </div>
        </div>
        <div class="span2" v-if="bgood">
          <span>{{bgood}}</span>
        </div>
          <div class="span3" v-if="cgood">
            <router-link to="/login"><div v-show="cgood==1">注册|登录</div></router-link>
            <div v-show="cgood==2" @click="btn1"><i class="el-icon-user-solid"></i></div>
            
        </div>
    </div>
</template>

<script>
export default {
  name: 'Head',
  props:['agood','bgood','cgood'],
  data(){
    return{
      
    }
  },
  methods:{
    btn1(){
      this.$router.push('/my')
    },
    
  }
}
</script>

<style lang="scss">
.header{
  box-sizing: border-box;
  width: 100%;
  height: .91rem;
  background: #3190e8;
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  z-index: 100;
  .span11{
    width: 25%;
    height:.91rem;
    font-size: .4rem;
    text-align: left;
    line-height: .91rem;
  }
  .span2{
    width: 48%;
    height: .91rem;
    font-size: .4rem;
    color: white;
    line-height: .91rem;
    text-align: center;
  }
  .span3{
    width: 25%;
    height: .91rem;
    font-size: .4rem;
    text-align: right;
    line-height: .91rem;
    color: white;
   a{
     color: white;
   }
  }
}
</style>



