<template>
  <div class="hello">
   <Head :agood='true' :bgood='bgood' :cgood='out'></Head>
    <!-- <div class="div"></div> -->
    <div class="content">
      <div class="zhi">
         <div>当前定位城市：</div>
         <div>定位不准时，请在城市列表中选择</div>
      </div>
      <div class="zhi1">
         <div><router-link to="/detail">{{location.name}}</router-link></div>
         <div>></div>
      </div>
      <!-- 热门城市 -->
    <div class="hot"> 
      <p>热门城市</p>
      <ul>
        <li v-for="(item,key) in list" :key="key">
          <router-link :to="'/detail?id='+`${item.id}`">{{item.name}}</router-link>
        </li>
      </ul>
      </div>  
      <!-- 全部城市 -->
      <div class="cities"  v-for="(item,key) in cher" :key="key">
        <p>{{item}}(按字母排序)</p>
        <ul>
          <li v-for="(v,i) in box[item]" :key="i" class="xu">
            <router-link :to="'/detail?id='+`${v.id}`">{{v.name}}</router-link>
          </li>
        </ul>
        
      </div>


    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Head from '@/components/head/Head'
export default {
  name: 'HelloWorld',
  components:{Head},
  data () {
    return {
      list:[],//热门城市
      box:[],//全部城市
      cher:[],//全部城市的字母排序
      location:[],//定位城市
      bgood:' ',
      out:''
    }
  },
  created(){
    
    if(localStorage.getItem('vuex')){
            this.out=2
        }else{
            this.out=1
        }
    //定位城市
    axios.get('http://elm.cangdu.org/v1/cities?type=guess').then((res)=>{
        // console.log(res.data)
        this.location=res.data
    })
    //热门城市
    axios.get('http://elm.cangdu.org/v1/cities?type=hot').then((res)=>{
      // console.log(res.data)
      this.list=res.data
    })
    //全部城市
    axios.get('http://elm.cangdu.org/v1/cities?type=group').then((res)=>{
      // console.log(res.data)
      this.box=res.data
      this.cher=Object.keys(res.data).sort()
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

p{
  padding: 0;
  margin: 0;
  font-size: 16px;
}
.content{
  margin-top: .91rem;
  background: #f5f5f5;
  width: 100%;
  .zhi{
    width: 100%;
    height: 0.57rem;
    display: flex;
    justify-content: space-between;
    background: white;
    margin-top: .1rem;
    border-bottom: 0.01rem solid gainsboro;
    div{
      &:nth-of-type(1){
        width: 1.8rem;
        height: 0.67rem;
        padding-left: 0.15rem;
        color: #666;
        font-size: 0.2rem;
        line-height: 0.7rem;
      }
      &:nth-of-type(2){
        width: 3.33rem;
        height: 0.67rem;
        padding-right: 0.15rem;
        color: #9f9f9f;
        font-weight: bold;
        font-size: 0.18rem;
        line-height: 0.67rem;
        text-align: right;
      }
    }
  }
  .zhi1{
    width: 100%;
    height: 0.84rem;
    line-height: .84rem;
    background: white;
    display: flex;
    justify-content: space-between;
     border-bottom: 0.02rem solid gainsboro;
    div{
      &:nth-of-type(1){
        width: .7rem;
        height: .84rem;
        font-size: .32rem;
        line-height: .84rem;
        padding-left: .1rem;
        a{
          text-decoration: none;
          color: #3190e8;
        }
      }
      &:nth-of-type(2){
        width: .28rem;
        height: .28rem;
        font-size: .32rem;
        padding-right:.1rem; 
        
      }
    }
  }
}
.hot{
  width: 100%;
  background: white;
  border-top: 0.02rem solid gainsboro;
  margin-top:.15rem;
  p{
    font-size: .22rem;
    color: #666;
    font-weight: 400;
    line-height: .7rem;
  }
  ul{
    background: white;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
   li{
     width: 24.7%;
     height: .5rem;
      text-align: center;
      border: 0.01rem solid gainsboro;
      line-height:.5rem;
      font-size: .2rem;
      a{
        text-decoration: none;
        color: #3190e8;
      }
   }
  }
}
.cities{
  widows: 100%;
  margin-top: .15rem;
  background: white;
  p{
    &:nth-of-type(1){
      height: .4rem;
      line-height: .4rem;
      border-top:0.01rem solid gainsboro; 
      border-bottom:0.01rem solid gainsboro; 
      font-size: .24rem;
    }
  }
  ul{
    background: white;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
   li{
     width: 24.7%;
     height: .5rem;
      text-align: center;
      border: 0.01rem solid gainsboro;
      line-height:.5rem;
      font-size: .2rem;
      overflow: hidden;
      text-overflow: ellipsisl;
      white-space: nowrap;
      a{
        text-decoration: none;
        color:black;
      }
   }
  }
}

</style>



