import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Detail from '@/components/Detail'
import Home from '@/components/Home'
import Login from '@/components/Login'
import hover from '@/components/cs/hover'
const headertop   = () => import('@/components/head/headertop')
const search   = () => import('@/components/search')
const order   = () => import('@/components/order')
const my   = () => import('@/components/my')
const logout   = () => import('@/components/logout')
const reset   = () => import('@/components/reset')
const goods   = () => import('@/components/goods')
const xin   = () => import('@/components/xin')
const addres   = () => import('@/components/addres')
const deta  = () => import('@/components/deta')
const left  = () => import('@/components/deta/left')
const right  = () => import('@/components/deta/right')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path:'/search',
      name:'search',
      component:search
    },
    {
      path:'/order',
      name:'order',
      component:order
    },
    {
      path:'/my',
      name:'my',
      component:my
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    {
      path:'/hover',
      name:'hover',
      component:hover
    },
    {
      path:'/logout',
      name:'logout',
      component:logout
    },
    {
      path:'/reset',
      name:'reset',
      component:reset
    },
    {
      path:'/headertop',
      name:'headertop',
      component:headertop
    },
    {
      path:'/goods',
      name:'goods',
      component:goods
    },
    {
      path:'/xin',
      name:'xin',
      component:xin
    },
    {
      path:'/addres',
      name:'addres',
      component:addres
    },
    {
      path:'/deta',
      name:'deta',
      component:deta,
      children:[
        {
          path:'/',
          redirect:'/deta/left'
        },
        {
          path:'left',
          name:'left',
          component:left
        },
        {
          path:'right',
          name:'right',
          component:right
        },
      ]
    }

    
  ]
})



