import Vue from 'vue'
import Vuex from 'vuex'
import VuePersist from 'vuex-persist'
const vuexLocal=new VuePersist({
	storage:window.localStorage
})
Vue.use(Vuex)

var store=new Vuex.Store({
	plugins:[vuexLocal.plugin],
	//定义初始数据{{this.$store.state.属性}}
	state:{
		login:{
			code:'',
			user:'',
			password:'',
			show:false,
			id:'',
			headPhoto:''
		},
		gou:[

		]
	},
	//深加工初始数据{{this.$store.getterss.方法名}}
	getters:{
		
	},
	//操作初始数据的方法{{this.$store.commit.('方法名')}}
	mutations:{
		add(state,val){	
			state.login = val
		},
		//图片
		imag(state,val){
			state.login.headPhoto=val
		},
		//购物车数据
		gou(state,val){
			state.gou.push(val)
		},
		//当购物车有的时候只加一
		addnum(state,val){
			state.gou[val].number++
		},
		//加
		add1(state,key){
			state.gou[key].number++
		},
		//减
		cut1(state,key){
			state.gou[key].number--
			if(state.gou[key].number<=0){
				state.gou.splice(key,1)
			}
		},
		// 清空
		del(state){
			state.gou.splice(0)
		}
		
	},
	//异步操作初始数据 其实就是调用mutations里面的方法{{this.$store.dispatch.方法}}
	actions:{
		
	}
})
export default store


