<template>
  <div class="hello">
   <Head :agood='true' :bgood='info' :cgood='out'></Head>
    <div class="content">
      <section>
        <p><input type="text" placeholder="输入学校、商务楼、地址" v-model="txt"/></p>
        <p><input type="button" value="提交" @click="sub()"/></p>
      </section>
      <div class="serch">
        
        <ul v-show="state==false"><div>搜索历史</div>
          <router-link to="/home" v-for="(v,key) in info1" :key="key" >
              <li>
                     <p>{{v.name}}</p>
                    <p>{{v.address}}</p>
                </li>
          </router-link> 
          <div class="clear" @click="clear()">清空所有</div>
        </ul>
        
      </div>
      <div class="hostory">
        <ul v-show="state==true">
         
           <router-link to="/home" v-for="(item,key) in list" :key="key" >
              <li @click="Local(item)">
                     <p>{{item.name}}</p>
                    <p>{{item.address}}</p>
                </li>
             </router-link> 
            
          
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Head from '@/components/head/Head'
import axios from 'axios'
export default {
  name: 'HelloWorld',
  components:{Head},
  data () {
    return {
      info:[],
      txt:'',
      list:[],
      info1:[],
      state:false,
      bgood:' ',
      out:''
    }
  },
  methods:{
    //提交按钮点击事件
    sub(){
      this.state=true
      axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.query.id+'&keyword='+this.txt).then((res)=>{
         console.log(res)
         this.list=res.data
       })
    },
    //路由跳转到新页面
    Local(item){
      this.state=true
      // console.log(item)
      var arr=JSON.parse(localStorage.getItem('data'))||[]
      var obj={name:item.name,address:item.address}
    
      var index=arr.findIndex((it,key,arr)=>{
        return item.name==it.name
      })
      if(index==-1){
        arr.unshift(obj)
      }
      localStorage.setItem('data',JSON.stringify(arr))
    },
    //清空所有
    clear(){
      this.info1=localStorage.removeItem('data')
      }
    
  },
  
  mounted(){
    if(localStorage.getItem('vuex')){
            this.out=2
        }else{
            this.out=1
        }
    //获取id
      var id=this.$route.query.id;
      // console.log(id)
      var path="http://elm.cangdu.org/v1/cities/"+id;
      axios.get(path).then((res)=>{
        // console.log(res.data)
        this.info=res.data.name
      })
      //从本地存储在
      this.info1=JSON.parse(localStorage.getItem('data'))||[]
      // console.log(this.info1)
  }
  
}
</script>


<style lang='scss' scoped>

body,html,#app{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background: gainsboro;
}
p{
  padding: 0;
  margin: 0;
  font-size: .24rem;
}

.content{
  margin-top: .91rem;
  width: 100%;
  height: 100%;
 
}
section{
  width: 100%;
  height: 1.5rem;
  background: white;
  margin-top: 0.15rem;
  display: flex;
  flex-direction: column;
    justify-content: center;
    align-items: center;
  p{
    margin: 0;
    padding: 0;
    width: 85%;
    height: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

     &:nth-of-type(1){
       input{
         width: 100%;
         height: 44%;
         
       }
     }
     &:nth-of-type(2){
       input{
         width: 100%;
         height: .5rem;
        background: #3190e8;
        border: none;
        color: white;
       }
     }
  }
}
.serch{
  background: white;
  div{
    width: 100%;
    height: .6rem;
    // background: gainsboro;
    border-bottom: 0.02rem solid grey;
    border-top: 0.02rem solid grey;
    font-size: .3rem;
    line-height: .6rem;
  }
 ul{
    list-style: none;
    margin: 0;
    padding: 0;
    li{
      width: 100%;
     overflow: hidden;
     text-overflow: ellipsisl;
      white-space: nowrap;
      height: 1.17rem;
     border-bottom: 0.01rem solid grey;
      color:black;
      p{
         margin-left: .2rem;
        &:nth-of-type(1){
          height: .8rem;
          line-height: .8rem;
          font-size: .3rem;
        }
        &:nth-of-type(2){ 
          width: 100%;
          height: .35rem;
          color: grey;
          overflow: hidden;
          text-overflow: ellipsisl;
          white-space: nowrap;
        }
      }
    }
    .clear{
    height: .8rem;
    font-size: .4rem;
    text-align: center;
    line-height: .8rem;
  }
  }
  
}
.hostory{
  width: 100%;
  height: 100%;
  background: white;

  ul{
    list-style: none;
    margin: 0;
    padding: 0;
    li{
     overflow: hidden;
      text-overflow: ellipsisl;
      white-space: nowrap;
      height: 1.17rem;
      width: 100%;
     border-bottom: 0.01rem solid grey;
      color:black;
      p{
         margin-left: .2rem;
        &:nth-of-type(1){
          height: .8rem;
          line-height: .8rem;
          font-size: .3rem;
        }
        &:nth-of-type(2){
          width: 100%;
          height: .35rem;
          color: grey;
          overflow: hidden;
      text-overflow: ellipsisl;
      white-space: nowrap;
        }
      }
    }
  }
}
</style>



