<template>
    <div class="ok">
    
        <Head :agood='true' :bgood='bgood' :cgood='3'></Head>
      
      <div id="content">
        <div class="login-s">
          <div>
            <input type="text" placeholder="账号" v-model="user"/>
          </div>
          <div>
            <input type="password" placeholder="密码" v-model="password" v-if='show' />
             <input type="text" placeholder="密码" v-model="password" v-else/>
             <div id="input">
                 <el-switch
              v-model="show"
              active-color="#13ce66"
              inactive-color="#ccc">
            </el-switch>
             </div>
            
          </div>
          <div>
            <input type="text" placeholder="验证码" maxlength="4" id="text" v-model="code"/>
            <div class="number" id="number"><img :src="numcode"></div>
           
            <button @click="one">看不清换一张</button>
              
           
          </div>
        </div>
        <!-- 按钮点击 -->
        <div class="tips">
          <p>温馨提示：未注册过的账号，登录时即自动登录</p>
          <p>注册过的用户可凭账号密码登录；</p>
          
        </div>
        <!-- 按钮 -->
        <div class="logina">
          <input type="button" value="登录" @click="add">
        </div>
        <!-- 重置密码 -->
        <div class="reset" @click="btn">重置密码？</div>
       </div>
    </div>
</template>
<script>
import axios from 'axios'
axios.defaults.withCredentials=true;// 让 axios 请求携带 cookie
import Head from '@/components/head/Head'
import { MessageBox } from 'mint-ui'
export default {
  name: 'Login',
  components:{Head},
  data(){
    return{
      bgood:'密码登录',
      user:'', // 账号
      password:'', // 密码 
      code:'',  // 验证码
      numcode:'',// 随机数
      show:false,
      id:''
    }
  },
  methods:{
    btn(){
      this.$router.push('/reset')
    },
    one(){
      axios.post("https://elm.cangdu.org/v1/captchas").then((res)=>{
     
      if(res.data.code!=''){
        // debugger;
        this.numcode=res.data.code
         console.log(res.data.code)
      }
      // console.log(res)
    })
  },
  add(){
    
        axios.post('http://elm.cangdu.org/v2/login',{
          captcha_code:this.code,
          username:this.user,
          password:this.password,
          id:this.id
        }).then((res)=>{
          // console.log(res)
          this.id = res.data.id
          // console.log(res.data.id)
          // console.log(re/s.data.message)
          // 看后台是否返回status 返回代表失败
          if(res.data.status==0){
          // 判读那输入框不为空
            if(this.user!==""&&this.password!==""&&this.code!==""){
              MessageBox(res.data.message)
            }
          }else{
            // localStorage.setItem('login',JSON.stringify({code:this.code,user:this.user,password:this.password}))
            var login = {code:this.code,user:this.user,password:this.password,show:true,id:this.id}
            this.$store.commit('add',login)
            this.$router.push('/my')
          }
          if(this.user==''){
              MessageBox('请输入账号')
          }else if(this.password==''){
              MessageBox('请输入密码')
          }else if(this.code==''){
              MessageBox('请输入验证码')
          }
        })
  }
},
  
  mounted(){
    this.one();
  },  
}
</script>

<style lang="scss">
#content{
  font-size:.24rem;
}

input,p{
  padding: 0;
  margin: 0;
  font-size: .24rem;
}
.login-s{
  background: white;
  margin-top: 1.2rem;
  
}
.login-s>div{
  
    width: 100%;
    height: .96rem;;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f1f1f1;
    padding: 0;
    margin: 0;
    
  
}
.login-s>div>input{
      border: none;
      margin-left: .3rem;
      width: 3.84rem;
      height: .39rem;
    }
.tips{
  width: 95%;
  height: 1.2rem;
  margin: 0 auto;
  font-size: .2rem;
  p{
    padding: .15rem 0 ;
    color: red;
  }
}
//
button{
  width: 1.1rem;
  height: .8rem;
  font-size: .24rem;
  border: none;
  background: none;
}
.logina{
  width: 95%;
  height: 1rem;
  margin: 0 auto;
  font-size: .2rem;
  input{
    width: 100%;
    height: 100%;
    background: #4cd964;
    border: none;
    border-radius: 0.05rem;
    font-size: .4rem;
    color: white;
  }
}
.number{
  width: .9rem;
  height: .5rem;
  font-size: .44rem;
  text-align: center;
  line-height: .44rem;
}
.reset{
  font-size: .3rem;
  color: #3190e8;
  text-align: right;
  padding: .3rem 0;
}
</style>



